/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header {
  width: 100%;
  min-width: $min-width;
  text-align: left;
  order: 1;
  -webkit-order: 1;
}

.header {
  display: none;
  padding: 2rem 0;
  border-bottom: 0.1rem solid $text-color;
  &-content {
    display: flex;
    align-items: center;
  }
  &-logo {
    max-width: 16rem;
    margin-right: auto;
    display: flex;
    img {
      width: 100%;
    }
  }
  &-user {
    margin-left: 2rem;
    width: 3.2rem;
    height: 3.2rem;
    &:hover {
      opacity: 0.7;
    }
    .aside-user__img {
      border: none;
    }
  }
  &-create {
    margin-left: 2rem;
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 50%;
    background-color: $red;
    color: #fff;
    text-align: center;
    line-height: 3.1rem;
    font-size: 2.8rem;
    &:hover {
      background: #ecbfc0;
    }
  }
}

.button-nav {
  position: relative;
  z-index: 5;
  margin-left: 2rem;
  z-index: 50;
  height: 30px;
  width: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
  span {
    backface-visibility: hidden;
    position: relative;
    background: #fff;
    transition: all 0.3s ease-out;
    width: 24px;
    height: 2px;
    display: block;
    margin: 0px 0 0px 0px;
  }
  span:before,
  span:after {
    transition: all 0.3s linear;
    backface-visibility: hidden;
    content: "";
    background: #fff;
    left: 0px;
    height: 2px;
    display: block;
    width: 24px;
    position: absolute;
  }
  span:before {
    top: 8px;
  }
  span:after {
    bottom: 8px;
  }
  &.active {
    span {
      background: transparent;
    }
    span:before {
      top: 0;
      transform: rotate(45deg);
    }
    span:after {
      bottom: 0px;
      transform: rotate(-45deg);
    }
  }
}

// media

@media screen and (max-width: 991px) {
  .header {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 1.5rem 0;
  }
}

@media screen and (max-width: 480px) {
  .header-logo {
    max-width: 14rem;
  }
  .header-create,
  .header-user,
  .button-nav {
    margin-left: 1.5rem;
  }
}
