//main styles

.main-wrapper {
  padding: 0 0 0 0;
  min-width: $min-width;
  width: 100%;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  justify-content: flex-start;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
}

.wrapper {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 40px 0 40px;
  margin: 0 auto;
  position: relative;
}

body {
  background-color: $base-bg;
}

/* text position */
.text-left {
  text-align: left !important;
}
.text-center {
  text-align: center !important;
}
.text-right {
  text-align: right !important;
}
.nowrap {
  white-space: nowrap !important;
}

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content {
  min-width: $min-width;
  text-align: left;
  width: 100%;
  order: 2;
  -webkit-order: 2;
  flex-grow: 1;
  -webkit-flex-grow: 1;
}

.btn {
  height: 4.1rem;
  line-height: 4.1rem;
  outline: none;
  border: none;
  background: #f33f3f;
  border-radius: 0.6rem;
  padding: 0 5.3rem;
  font-weight: 700;
  font-size: 1.8rem;
  color: #141414;
  display: inline-block;
  transition: $transition;
  font-family: "Lato", sans-serif;
  display: flex;
  align-items: center;
  i {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #d9d9d9;
    border-radius: 0.8rem;
    width: 2.2rem;
    height: 2.2rem;
    line-height: 2.2rem;
    margin-right: 1.5rem;
    font-size: 2.2rem;
    font-style: normal;
  }

  &:hover {
    background: #ecbfc0;
  }
  &-list {
    justify-content: flex-start;
    background: #000000;
    color: $text-color;
    font-size: 1.6rem;
    padding: 0 1.2rem;
    &:hover {
      background: #000000;
      opacity: 0.7;
    }
  }
  &-new {
    justify-content: flex-start;
    padding: 0 1.2rem;
    font-size: 1.6rem;
  }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.form-control {
  height: 4.5rem;
  line-height: normal;
  border: 0.1rem solid #e1e1e1;
  border-radius: 0.6rem;
  outline: none;
  box-shadow: none;
  padding: 0 2.2rem;
  font-size: 1.6rem;
  color: #e1e1e1;
  background-color: transparent;
  width: 100%;
  transition: $transition;
  &::placeholder {
    color: rgba(217, 217, 217, 0.3);
    opacity: 1;
  }

  &:-ms-input-placeholder {
    color: rgba(217, 217, 217, 0.3);
  }

  &::-ms-input-placeholder {
    color: rgba(217, 217, 217, 0.3);
  }
}

.form-box {
  position: relative;
  &__search {
    .form-control {
      padding-left: 5rem;
    }
    .icon-search {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 1.2rem;
      font-size: 2.1rem;
      &:before {
        color: rgba(217, 217, 217, 0.3);
      }
    }
    .btn {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      height: calc(100% + 0.2rem);
      line-height: normal;
      font-size: 1.6rem;
    }
  }
  &__label {
    font-weight: 700;
    font-size: 1.8rem;
    line-height: 2.2rem;
    color: #e1e1e1;
    margin-bottom: 0.5rem;
    display: block;
  }
  textarea {
    padding: 1.3rem 2.2rem;
    resize: none;
  }
}

.icon-m {
  &:before {
    content: "\e908";
    color: rgb(217, 217, 217);
  }
  &:after {
    content: "\e909";
    margin-left: -1em;
    color: rgb(0, 0, 0);
  }
}

.main {
  display: flex;
  flex-wrap: wrap;
  &-aside {
    width: 32rem;
    background: #161616;
    padding: 3rem;
    display: flex;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 10;
    &__box {
      display: flex;
      flex-direction: column;
      overflow: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      width: 100%;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    &__overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      content: "";
      z-index: -1;
      background-color: rgba(0, 0, 0, 0.3);
      transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
      opacity: 0;
      pointer-events: none;
      visibility: hidden;
      &.active {
        opacity: 1;
        pointer-events: all;
        visibility: visible;
      }
    }
    &__search {
      margin-top: 2.5rem;
      width: 100%;
      .form-box {
        .form-control {
          border-color: rgba(217, 217, 217, 0.3);
          height: 3.5rem;
          &:focus {
            border-color: #e1e1e1;
          }
        }
      }
    }
    &__nav {
      margin-top: 4rem;
      li {
        margin-bottom: 2rem;
        &:last-of-type {
          margin-bottom: 0;
        }
        a {
          padding: 1rem 1.2rem;
          transition: $transition;
          font-size: 1.6rem;
          line-height: 1.9rem;
          text-align: center;
          color: $text-color;
          display: flex;
          align-items: center;
          border-radius: 0.6rem;
          &:hover {
            color: $red;
            i:before {
              color: $red;
            }
          }
          &.active {
            background: #1f1f1f;
            &:hover {
              color: $text-color;
              i:before {
                color: $text-color;
              }
            }
          }
          i {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 1.5rem;
            font-size: 2.1rem;
            &:before {
              transition: $transition;
            }
          }
        }
      }
    }
    .btn {
      margin-top: 3rem;
      width: 100%;
      font-weight: 700;
      font-size: 1.6rem;
    }
  }
  &-logo {
    max-width: 21.2rem;
    img {
      width: 100%;
    }
  }
  &-content {
    width: calc(100% - 32rem);
    margin-left: auto;
    padding: 3rem;
    &__title {
      display: block;
      font-size: 3.2rem;
      line-height: 3.8rem;
      color: $text-color;
      &-link {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }
  &-welcome {
    width: 100%;
    background: rgba(217, 217, 217, 0.1);
    border: 0.1rem solid #a41b1b;
    border-radius: 0.8rem;
    padding: 2rem;
    &__title {
      font-weight: 400;
      font-size: 4rem;
      line-height: 4.8rem;
      color: $text-color;
      display: block;
      margin-bottom: 2.5rem;
      span {
        color: $red;
      }
    }
    p {
      font-weight: 400;
      font-size: 2rem;
      line-height: 2.4rem;
      color: $text-color;
      i,
      img {
        display: inline-block;
        vertical-align: middle;
        margin: 0 0.5rem;
      }
    }
  }
  &-search {
    margin-top: 4rem;
    .form-box__search {
      .btn {
        width: 9.1rem;
        padding-left: 2.2rem;
        padding-right: 2.2rem;
      }
      .form-control {
        padding-right: 11.1rem;
      }
    }
  }
}
.aside-lists {
  margin-top: 2rem;
  border-top: 0.1rem solid rgba(217, 217, 217, 0.3);
  padding: 1.8rem 0;
  &__title {
    display: block;
    color: #9a9a9a;
    letter-spacing: 0.05em;
    font-weight: 400;
    font-size: 2rem;
    line-height: 2.4rem;
  }
  ul {
    margin-top: 2rem;
    li {
      a {
        padding: 1rem 1.2rem;
        transition: $transition;
        font-size: 1.6rem;
        line-height: 1.9rem;
        text-align: center;
        color: $text-color;
        display: flex;
        align-items: center;
        border-radius: 0.6rem;
        &:hover {
          opacity: 0.7;
        }
        i {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 1.5rem;
          font-size: 2.1rem;
        }
      }
    }
  }
}

.aside-user {
  margin-top: auto;
  border: 0.1rem solid #e1e1e1;
  border-radius: 0.4rem;
  padding: 0.6rem 1rem;
  display: flex;
  align-items: center;
  &:hover {
    opacity: 0.7;
  }
  &__img {
    border: 0.2rem solid #1f1f1f;
    border-radius: 50%;
    overflow: hidden;
    background: #e1e1e1;
    width: 3.2rem;
    height: 3.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__name {
    color: $text-color;
    font-size: 1.2rem;
    line-height: 1.4rem;
    margin-left: 1rem;
  }
  i {
    &.icon-dots {
      font-size: 0.4rem;
      margin-left: auto;
    }
  }
}

.movies-list {
  &__popular {
    margin-top: 3.5rem;
  }
  &__title {
    display: block;
    font-size: 3.2rem;
    line-height: 3.8rem;
    color: $text-color;
  }
  &__box {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -3rem;
  }
}
.movies-item {
  width: calc(20% - 6rem);
  margin: 3rem;
  background: #1f1f1f;
  border-radius: 0.4rem;
  position: relative;
  transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  will-change: transform;
  &:hover {
    transform: scale(1.1);
  }
  &__img {
    width: 100%;
    // height: 22.5rem;
    border-radius: 0.4rem 0.4rem 0 0;
    position: relative;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      content: "";
      background-color: rgba(0, 0, 0, 0.3);
      transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
      opacity: 0;
      pointer-events: none;
    }
  }
  &__add {
    position: absolute;
    left: 0;
    top: 0;
    width: 2.4rem;
    height: 3.5rem;
    display: block;
    background-image: url(../../img/icons/ribbon.svg);
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 1;
    transition: $transition;
    &:hover {
      opacity: 0.7;
    }
  }
  &__rate {
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 1.6rem;
    line-height: 1.9rem;
    color: $text-color;
    i {
      margin-right: 0.5rem;
      font-size: 2.1rem;
    }
    sup {
      font-size: 1rem;
    }
  }
  &__title {
    margin-top: 0.5rem;
    font-size: 1.6rem;
    line-height: 1.9rem;
    display: block;
    color: $text-color;
    span {
      font-weight: 300;
    }
  }
  &__watched {
    position: absolute;
    right: 1rem;
    top: 1.5rem;
    i {
      font-size: 2.5rem;
      &:before {
        transition: $transition;
      }
    }
  }
  &.watched {
    .movies-item__img:after {
      opacity: 1;
    }
    .movies-item__watched {
      i {
        &:before {
          color: #00bb34;
        }
      }
    }
  }
}

.popup {
  visibility: hidden;
  opacity: 0;
  transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  &.active {
    opacity: 1;
    visibility: visible;
    .popup-box {
      transform: translateX(-50%) translateY(-50%) scale(1);
    }
  }
  &-overlay {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  &-box {
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) scale(0.8);
    position: absolute;
    width: 70rem;
    max-width: 100%;
    min-height: 30rem;
    background: #161616;
    border-radius: 0.4rem;
    padding: 3rem;
    border: 0.1rem solid #e1e1e1;
  }
  &-close {
    position: absolute;
    font-size: 2.2rem;
    top: 1.5rem;
    right: 1.5rem;
    &:hover {
      opacity: 0.7;
    }
  }
  &-add {
    &__content {
      li {
        margin-bottom: 1.5rem;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: $text-color;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    &__title {
      display: block;
      width: 100%;
      margin-top: 0.8rem;
      font-size: 2rem;
      line-height: 2.4rem;
    }
    &__list {
      width: 100%;
      margin-top: 2.1rem;
      li {
        margin-bottom: 1.5rem;
      }
    }
    .btn {
      max-width: 24.7rem;
      width: 100%;
      margin-bottom: 1.5rem;
      &-new {
        margin-bottom: 0;
      }
    }
  }
  &-new {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__form {
      width: 65.7%;
      .form-box {
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        &__label {
          width: 10rem;
          min-width: 10rem;
          padding-right: 2rem;
          padding-top: 1rem;
          margin-bottom: 0;
        }
        .form-control {
          max-width: 29.5rem;
          width: 100%;
        }
        textarea {
          padding: 1.3rem 2.2rem;
          height: 14.1rem;
          resize: none;
        }
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    &__btns {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      align-self: flex-end;
      width: 34.3%;
      a {
        font-weight: 700;
        font-size: 1.6rem;
        line-height: 1.9rem;
        text-decoration-line: underline;
        color: $red;
        margin-left: 3rem;
        &:hover {
          color: #ecbfc0;
        }
      }
      .btn {
        color: #000000;
        text-decoration: none;
        line-height: normal;
        width: 11rem;
        padding: 0;
        justify-content: center;
        margin-left: 3rem;
        &:hover {
          color: #000000;
        }
      }
    }
    &__title {
      width: 100%;
      display: block;
      margin-bottom: 2rem;
      color: $text-color;
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.4rem;
    }
  }
}
#popupNew {
  .popup-box {
    min-height: auto;
  }
}

.main-create {
  margin-top: 5rem;
  &__form {
    .form-box {
      margin-bottom: 3rem;
      &__label {
        margin-bottom: 0.5rem;
        display: block;
      }
      textarea {
        height: 12.6rem;
      }
    }
    .btn {
      min-width: 24.7rem;
      justify-content: center;
    }
  }
}

.main-login {
  margin-top: 10.3rem;
  display: flex;
  justify-content: center;
  &__form {
    max-width: 34.9rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .form-box {
      margin-bottom: 2rem;
    }
    .btn {
      margin-top: 3rem;
      min-width: 24.7rem;
      align-self: center;
      justify-content: center;
    }
    &-or {
      font-weight: 700;
      font-size: 1.8rem;
      text-align: center;
      color: #ffffff;
      margin-top: 1.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        color: $red;
        margin-left: 0.5rem;
        &:hover {
          color: #ecbfc0;
        }
      }
    }
  }
}

.link {
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1.9rem;
  color: $red;
  text-decoration-line: underline;
  &:hover {
    color: #ecbfc0;
  }
}

.main-history {
  display: flex;
  flex-direction: column;
  &__clear {
    align-self: flex-end;
    margin-bottom: 1rem;
  }
}

.watchlist-detail {
  &__title {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 3.8rem;
    line-height: 4.6rem;
    color: $text-color;
  }
  &__edit {
    display: flex;
    margin-left: 5rem;
    i {
      font-size: 2.5rem;
      &:before {
        transition: $transition;
      }
    }
    &:hover {
      i:before {
        color: $red;
      }
    }
  }
  &__desc {
    margin-top: 2.6rem;
    &-title {
      display: block;
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.4rem;
      color: $text-color;
      margin-bottom: 1rem;
    }
    p {
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.9rem;
      color: $text-color;
    }
  }
  &__nums {
    margin-top: 7rem;
    display: flex;
    li {
      display: flex;
      align-items: center;
      flex-direction: column;
      background: rgba(217, 217, 217, 0.1);
      border: 0.1rem solid #a41b1b;
      border-radius: 0.8rem;
      padding: 1.5rem;
      font-weight: 700;
      font-size: 3.8rem;
      line-height: 4.6rem;
      color: $red;
      margin-right: 4rem;
      &:last-of-type {
        margin-right: 0;
      }
      span {
        font-size: 2rem;
        line-height: 2.4rem;
        text-transform: uppercase;
        display: block;
        margin-bottom: 2.1rem;
        color: $text-color;
      }
    }
  }
  &__list {
    margin-top: 3rem;
  }
}

.main-results {
  margin-top: 1.5rem;
}

.main-edit {
  margin-top: 5rem;
  &__list {
    margin-bottom: 4rem;
    &-title {
      display: block;
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 2.2rem;
      color: #e1e1e1;
      margin-bottom: 0.5rem;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    border: 0.1rem solid #e1e1e1;
    border-radius: 0.4rem;
    margin-bottom: 1rem;
    overflow: hidden;
    &:last-of-type {
      margin-bottom: 0;
    }
    &-img {
      width: 5rem;
      height: 7.5rem;
      &:hover {
        opacity: 0.7;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-title {
      margin-left: 1.5rem;
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.9rem;
      color: #e1e1e1;
      span {
        font-weight: 300;
      }
      &:hover {
        opacity: 0.7;
      }
    }
    .btn {
      margin-left: auto;
      margin-right: 1.5rem;
      min-width: 7.7rem;
    }
  }
}

.btn-border {
  background: #1a1a1a;
  border: 0.1rem solid #f33f3f;
  border-radius: 0.4rem;
  padding: 1rem;
  height: 4rem;
  font-size: 1.6rem;
  color: #e1e1e1;
  &:hover {
    background-color: #f33f3f;
  }
}

#upload-photo {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.form-box__photo {
  label {
    display: flex;
    flex-direction: column;
    align-items: center;
    span {
      display: flex;
      align-items: center;
      margin-top: 0.8rem;
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.4rem;
      color: #e1e1e1;
      i {
        margin-right: 1.5rem;
      }
    }
  }
  &-img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    background: #c4c4c4;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      border-radius: 50%;
      object-fit: cover;
    }
  }
}

.movie-detail {
  &__main {
    display: flex;
    align-items: center;
  }
  &__img {
    width: 30rem;
    height: 44.4rem;
    border-radius: 0.6rem;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 0.6rem;
    }
  }
  &__info {
    padding-left: 4.5rem;
    width: calc(100% - 30rem);
  }
  &__title {
    font-weight: 700;
    font-size: 3.6rem;
    line-height: 4.3rem;
    color: #e1e1e1;
    display: block;
    span {
      font-weight: 300;
    }
  }
  &__subtitle {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    span {
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.9rem;
      color: #ffffff;
      padding-left: 1.5rem;
      margin-left: 1.5rem;
      position: relative;
      &:before {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        content: "";
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
        background: #d9d9d9;
      }
      &:first-of-type {
        margin-left: 0;
        padding-left: 0;
        &:before {
          display: none;
        }
      }
    }
  }
  &__overview {
    margin-top: 5rem;
    span {
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.4rem;
      color: #ffffff;
    }
    p {
      margin-top: 1rem;
      font-size: 1.6rem;
      line-height: 1.9rem;
      color: #e1e1e1;
    }
  }
  &__add {
    margin-top: 5.6rem;
    display: flex;
    align-items: center;
    .btn {
      margin-left: 9rem;
      padding: 0 3.2rem;
      height: 6.3rem;
    }
  }
  &__score {
    width: 8.1rem;
    padding: 1.5rem;
    text-align: center;
    background: rgba(217, 217, 217, 0.1);
    border: 0.1rem solid #a41b1b;
    border-radius: 0.8rem;
    font-weight: 700;
    font-size: 3.6rem;
    line-height: 4.3rem;
    color: #f33f3f;
    span {
      display: block;
      color: #e1e1e1;
      font-size: 2rem;
      line-height: 2.4rem;
      margin-bottom: 0.5rem;
    }
  }
  &__cast {
    margin-top: 4.5rem;
    &-title {
      display: block;
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.4rem;
      color: #ffffff;
    }
    &-box {
      display: flex;
      margin-top: 2rem;
      overflow: auto;
    }
    &-item {
      width: 15rem;
      min-width: 15rem;
      margin-right: 4rem;
      padding: 1.5rem;
      background: #1a1a1a;
      border-radius: 0.4rem;
      overflow: hidden;
      &:last-of-type {
        margin-right: 0;
      }
      &-img {
        width: calc(100% + 3rem);
        margin: 0 -1.5rem;
        height: 19rem;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    &-name {
      margin-top: 1.5rem;
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 1.9rem;
      color: #e1e1e1;
      display: block;
    }
    &-role {
      display: block;
      margin-top: 1.5rem;
      font-weight: 400;
      font-size: 1.4rem;
      line-height: 1.7rem;
      color: #e1e1e1;
    }
  }
  &__list {
    margin-top: 9.8rem;
  }
}

// media

@media screen and (max-width: 1600px) {
  .wrapper {
    padding: 0 2rem;
  }
  .movies-list__box {
    margin: 0 -2rem;
  }
  .movies-item {
    width: calc(20% - 4rem);
    margin: 2rem;
  }
  .main-content {
    padding: 3rem 2rem;
    padding-right: 0;
  }
  .main-aside {
    padding: 3rem 2rem;
  }
}

@media screen and (max-width: 1200px) {
  .main-aside {
    width: 30rem;
  }
  .main-content {
    width: calc(100% - 30rem);
  }
  .movies-item {
    width: calc(33.3% - 4rem);
  }
}

@media screen and (max-width: 991px) {
  .main-aside {
    transform: translateX(-100%);
    transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    left: 0;
    z-index: 100;
    &.active {
      transform: translateX(0);
    }
  }
  .main-content {
    width: 100%;
    padding: 3rem 0;
  }
}

@media screen and (max-width: 767px) {
  .movies-item {
    width: calc(50% - 4rem);
  }
  .wrapper {
    padding: 0 1.5rem;
  }
  .main-welcome {
    padding: 1.5rem;
    p {
      font-size: 1.6rem;
      line-height: 2rem;
    }
  }
  .main-welcome__title {
    font-size: 3rem;
    margin-bottom: 1.5rem;
  }
  .main-welcome p i,
  .main-welcome p img {
    width: 1.6rem;
    font-size: 1.4rem;
  }
  .main-search,
  .movies-list__popular {
    margin-top: 3rem;
  }
  .movies-list__title {
    font-size: 2.6rem;
    line-height: 3.2rem;
    margin-bottom: 1.5rem;
  }
  .popup-box {
    padding: 3rem 1.5rem;
    width: calc(100% - 3rem);
  }
  .popup-close {
    font-size: 1.6rem;
  }
  .popup-new__form {
    width: 100%;
    .form-box {
      flex-direction: column;
      &__label {
        width: 100%;
        margin-bottom: 0.8rem;
        font-size: 1.6rem;
      }
      .form-control {
        max-width: 100%;
      }
    }
  }
  .popup-new__btns {
    width: 100%;
    margin-top: 2rem;
    justify-content: space-between;
    a,
    .btn {
      margin: 0;
    }
  }
  .main-create {
    margin-top: 3rem;
  }
  .main-content__title {
    font-size: 2.8rem;
    line-height: 3.2rem;
  }
  .main-create__form .form-box {
    margin-bottom: 1.5rem;
  }
  .main-login {
    margin-top: 3rem;
  }
  .main-login__form .btn {
    margin-top: 1rem;
  }
  .watchlist-detail__title {
    font-size: 2.8rem;
    line-height: 3.2rem;
  }
  .watchlist-detail__desc {
    margin-top: 3rem;
  }
  .watchlist-detail__nums {
    flex-wrap: wrap;
    margin-top: 3rem;
  }
  .watchlist-detail__nums li {
    width: 100%;
    margin-right: 0;
    margin-bottom: 1.5rem;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  .main-content__title-link {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: flex-start;
    .link {
      margin-top: 1.5rem;
    }
  }
  .main-edit__item {
    flex-wrap: wrap;
    position: relative;
    flex-direction: column;
    padding: 3rem 0;
    padding-right: 1.5rem;
    min-height: 25rem;
  }
  .main-edit__item-img {
    width: calc(50% - 0.75rem);
    height: auto;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
  }
  .main-edit__item-title {
    width: 50%;
    margin-left: auto;
    padding-left: 1.5rem;
  }
  .main-create__form .main-edit__item .btn {
    margin: 0;
    margin-top: auto;
    margin-left: auto;
    width: calc(50% - 1.5rem);
  }
  .main-create__form .btn {
    width: 100%;
  }
  .movie-detail__main {
    flex-direction: column;
  }
  .movie-detail__info {
    width: 100%;
    margin-top: 3rem;
    padding: 0;
  }
  .movie-detail__title {
    font-size: 2.8rem;
  }
  .movie-detail__overview {
    margin-top: 3rem;
  }
  .movie-detail__add {
    justify-content: space-between;
    .btn {
      width: calc(100% - 9.6rem);
      margin: 0;
      justify-content: center;
    }
  }
  .movie-detail__cast,
  .movie-detail__list {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 480px) {
  .movies-item {
    width: calc(50% - 3rem);
    margin: 1.5rem;
  }
  .movies-list__box {
    margin: 0 -1.5rem;
  }
  .watchlist-detail__nums li span {
    font-size: 3.2rem;
    line-height: 3.8rem;
  }
  .watchlist-detail__nums li span {
    font-size: 1.8rem;
    line-height: 2.2rem;
    margin-bottom: 1.5rem;
  }
}
